<template>
  <v-container>
    <v-row>
      <v-dialog
        v-model="show"
        :persistent="dialog.persistent ? dialog.persistent : true"
        :max-width="dialog.maxWidth ? dialog.maxWidth : '600px'"
      >
        <v-card class="p-10">
          <v-card-title>
            {{ dialog.title ? dialog.title : $t('crud.deleteDialog.title') }}
          </v-card-title>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              :color="
                dialog.cancelButtonColor ? dialog.cancelButtonColor : 'error'
              "
              @click="closeConfirm"
              >{{
                dialog.cancelButtonText
                  ? dialog.cancelButtonText
                  : $t('crud.deleteDialog.closeButton.text')
              }}</v-btn
            >
            <v-btn
              type="submit"
              dark
              :color="
                dialog.deleteButtonColor ? dialog.deleteButtonColor : 'success'
              "
              :loading="loading.delete"
              @click="save"
              >{{
                dialog.deleteButtonText
                  ? dialog.deleteButtonText
                  : $t('crud.deleteDialog.deleteButton.text')
              }}</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>

<script>
export default {
  props: {
    loading: {
      type: Object,
      required: true
    },
    show: {
      type: Boolean,
      default: false
    },
    dialog: {
      type: [Object, Boolean],
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {}
  },
  methods: {
    closeConfirm() {
      this.$emit('close')
    },
    save() {
      this.$emit('success')
    }
  }
}
</script>
